"use client";

import { useState } from "react";
import { ShipCard } from "@/components/game/ShipCard";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";

// Mock data for initial ships
const initialShips = [
  {
    id: "1",
    name: "Stellar Vanguard",
    type: "Battleship",
    level: 3,
    imageUrl: "https://images.pexels.com/photos/12498752/pexels-photo-12498752.jpeg",
    stats: {
      attack: 75,
      defense: 85,
      speed: 60,
      energy: 90
    }
  },
  {
    id: "2",
    name: "Nova Scout",
    type: "Scout Ship",
    level: 2,
    imageUrl: "https://images.pexels.com/photos/12498744/pexels-photo-12498744.jpeg",
    stats: {
      attack: 45,
      defense: 50,
      speed: 95,
      energy: 70
    }
  },
  {
    id: "3",
    name: "Quantum Destroyer",
    type: "Destroyer",
    level: 4,
    imageUrl: "https://images.pexels.com/photos/12498769/pexels-photo-12498769.jpeg",
    stats: {
      attack: 90,
      defense: 70,
      speed: 65,
      energy: 85
    }
  }
];

export default function FleetPage() {
  const [ships, setShips] = useState(initialShips);
  const [selectedShipId, setSelectedShipId] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleUpgrade = (shipId: string) => {
    try {
      setShips(ships.map(ship => {
        if (ship.id === shipId) {
          // Simulate upgrade by increasing stats
          return {
            ...ship,
            level: ship.level + 1,
            stats: {
              attack: Math.min(100, ship.stats.attack + 5),
              defense: Math.min(100, ship.stats.defense + 5),
              speed: Math.min(100, ship.stats.speed + 5),
              energy: Math.min(100, ship.stats.energy + 5)
            }
          };
        }
        return ship;
      }));
    } catch (err) {
      setError("Failed to upgrade ship. Please try again.");
    }
  };

  const handleSelect = (shipId: string) => {
    setSelectedShipId(shipId);
  };

  return (
    <div className="min-h-screen bg-black text-white p-8">
      {/* Header */}
      <div className="max-w-7xl mx-auto mb-8">
        <h1 className="text-4xl font-bold mb-4 bg-gradient-to-r from-blue-500 to-purple-600 bg-clip-text text-transparent">
          Fleet Management
        </h1>
        <p className="text-gray-400 mb-6">
          Manage and upgrade your fleet of ships to dominate the galaxy
        </p>
        
        {error && (
          <Alert variant="destructive" className="mb-6">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {selectedShipId && (
          <div className="bg-gray-900 p-4 rounded-lg mb-6 flex items-center justify-between">
            <p className="text-blue-400">
              Selected Ship: {ships.find(s => s.id === selectedShipId)?.name}
            </p>
            <Button 
              variant="outline"
              onClick={() => setSelectedShipId(null)}
              className="border-blue-500 text-blue-500 hover:bg-blue-950"
            >
              Clear Selection
            </Button>
          </div>
        )}
      </div>

      {/* Fleet Grid */}
      <div className="max-w-7xl mx-auto">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {ships.map((ship) => (
            <ShipCard
              key={ship.id}
              {...ship}
              onUpgrade={handleUpgrade}
              onSelect={handleSelect}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
