"use client";

import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";
import Image from "next/image";

interface ShipStats {
  attack: number;
  defense: number;
  speed: number;
  energy: number;
}

interface ShipCardProps {
  id: string;
  name: string;
  type: string;
  level: number;
  imageUrl: string;
  stats: ShipStats;
  onUpgrade?: (id: string) => void;
  onSelect?: (id: string) => void;
}

export function ShipCard({
  id,
  name,
  type,
  level,
  imageUrl,
  stats,
  onUpgrade,
  onSelect,
}: ShipCardProps) {
  return (
    <Card className="overflow-hidden bg-gray-900 border-gray-800 transition-all hover:border-blue-500">
      <div className="relative h-48 w-full">
        <Image
          src={imageUrl}
          alt={name}
          fill
          className="object-cover"
          onError={(e) => {
            e.currentTarget.src = "https://images.pexels.com/photos/2150/sky-space-dark-galaxy.jpg";
          }}
        />
        <div className="absolute top-2 right-2 bg-black/60 px-2 py-1 rounded-full">
          <span className="text-sm text-blue-400">Level {level}</span>
        </div>
      </div>

      <div className="p-4">
        <h3 className="text-xl font-bold text-white mb-1">{name}</h3>
        <p className="text-sm text-gray-400 mb-4">{type}</p>

        <div className="space-y-3 mb-4">
          <StatBar label="Attack" value={stats.attack} />
          <StatBar label="Defense" value={stats.defense} />
          <StatBar label="Speed" value={stats.speed} />
          <StatBar label="Energy" value={stats.energy} />
        </div>

        <div className="flex gap-2">
          <Button
            variant="outline"
            className="flex-1 border-blue-500 text-blue-500 hover:bg-blue-950"
            onClick={() => onSelect?.(id)}
          >
            Select
          </Button>
          <Button
            className="flex-1 bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700"
            onClick={() => onUpgrade?.(id)}
          >
            Upgrade
          </Button>
        </div>
      </div>
    </Card>
  );
}

function StatBar({ label, value }: { label: string; value: number }) {
  return (
    <div>
      <div className="flex justify-between text-sm mb-1">
        <span className="text-gray-400">{label}</span>
        <span className="text-blue-400">{value}%</span>
      </div>
      <Progress value={value} className="h-2" />
    </div>
  );
}
